body {
  font: 1em "Century Gothic", Futura, sans-serif;
  margin: 2em;
  background: rgba(0, 128, 0, 0.5)
    url("https://images.unsplash.com/photo-1521798948753-02f843fd3e5d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  height: -webkit-fill-available;
}
button {
  color: white;
  font-size: xx-small;
}
.board-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 12vmin;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
  font-size: 1em;
  text-align: center;
  position: fixed;
  z-index: 999;
  color: blanchedalmond;
  font-weight: 700;
}

.square {
  background: rgba(255, 255, 255, 0.089);
  border: 0.1em solid rgb(24, 9, 9);
  /* float: left; */
  color: white;
  font-weight: 700;
  -webkit-font-variant-ligatures: additional-ligatures;
          font-variant-ligatures: additional-ligatures;
  font-weight: bold;
  font-size: xx-large;
  line-height: 3em;
  height: 5em;
  border-radius: 0.3em;
  text-align: center;
  width: auto;
}

.square:focus {
  outline: none;
}
.square:hover {
  background: rgba(172, 255, 47, 0.452);
}

.kbd-navigation .square:focus {
  background: #ddd;
  background: white;
}

.game {
  display: grid;
}

.game-info {
  margin-left: 20px;
}



